.house {
  position: relative;
  height: 120px;
  padding-top: 18px;
  justify-content: space-around;
  border-bottom: 1px solid #e5e5e5;
  cursor: pointer;
  box-sizing: border-box;
}

.imgWrap {
  float: left;
  width: 106px;
  height: 80px;
}

.img {
  width: 106px;
  height: 80px;
}

.content {
  overflow: hidden;
  padding-left: 12px;
  line-height: 22px;
}

.title {
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  font-size: 15px;
  color: #394043;
}

.desc {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  font-size: 12px;
  color: #afb2b3;
}

.price {
  font-size: 12px;
  color: #fa5741;
}

.priceNum {
  font-size: 16px;
  font-weight: bolder;
}

.tagList {
  width: 100%;
  height: 24px;
  overflow: hidden;
}

.tag {
  display: inline-block;
  padding: 4px 5px;
  margin-right: 5px;
  font-size: 12px;
  line-height: 12px;
  border-radius: 3px;
}

.tag1 {
  background: #e1f5f8;
  color: #39becd;
}

.tag2 {
  background: #e1f5ed;
  color: #39cd72;
}

.tag3 {
  background: #e6f2ff;
  color: #cd398a;
}

.tag4 {
  background: #e6f2ff;
  color: #e6c01b;
}