.map {
  height: 100%;
  padding-top: 45px;
}

.container {
  height: 100%;
  z-index: 1;
}

.map :global(.am-navbar) {
  margin-top: -45px;
}

/* 覆盖物样式： */
.bubble {
  display: inline-block;
  position: absolute;
  width: 50px;
  height: 50px;
  background: #0ca893e6;
  border: 1px solid #ffffffcc;
  line-height: 1;
  color: #fff;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
}

.name {
  padding: 10px 0 4px 0;
}

/* 小区的覆盖物样式： */
.rect {
  position: absolute;
  height: 26px;
  width: 100px;
  padding: 0 3px;
  background: #0ca893e6;
  line-height: 26px;
  color: #fff;
  cursor: pointer;
  white-space: nowrap;
  border-radius: 5px;
}

.arrow {
  display: block;
  width: 0;
  height: 0;
  margin: 0 auto;
  border: 4px solid transparent;
  border-top-color: #0ca893;
}

.housename {
  display: inline-block;
  width: 70px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
}

.housenum {
  display: inline-block;
  width: 20px;
}

/* 房源列表样式： */
.houseList {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 330px;
  background: #fff;
  transition: all 1s;
  transform: translateY(330px);
}

.show {
  transform: translate3d(0, 0, 0);
}

.titleWrap {
  position: relative;
  width: 100%;
  background: #09e7b0;
  border-top: 1px solid #c8c8c8;
}

.listTitle {
  display: inline-block;
  padding-left: 10px;
  line-height: 43px;
  font-size: 16px;
  color: #1e1e1e;
  vertical-align: middle;
}

.titleMore {
  float: right;
  padding-right: 15px;
  line-height: 43px;
  font-size: 13px;
  color: #1e1e1e;
  vertical-align: middle;
}

.titleMore:visited {
  text-decoration: none;
}

/* 房屋列表项样式 */
.houseItems {
  height: 100%;
  padding: 0 10px;
  overflow-y: auto;
  padding-bottom: 45px;
}